<template>
<div id="music">
	<md-layout md-gutter>
		 <md-layout md-flex-xsmall="50" md-flex-small="50" md-flex-medium="33" v-for="(album, index) of albums">
			 <img class="album-img" :src="album.bg" @click="showList(index)">
		 </md-layout>
	</md-layout>
</div>
	
</template>
<script>
import Search from "./search_music.vue"
import backToTop from "../common/backToTop.vue"
import axios from "axios"
export default {
  components: {

  },
  data() {
	return {
		albums:[
			{
				name: "飙升榜",
				id:19723756,
				bg: require("./img/soar_music.jpg")
			},
			{
				name: "新歌榜",
				id:3779629,
				bg:require("./img/new_music.jpg")
			},
			{
				name: "原创榜",
				id:2884035,
				bg:require("./img/original_music.jpg")
			},
			{
				name: "KTV麦榜",
				id:21845217,
				bg:require("./img/ktv_music.jpg")
			},
			{
				name: "华语金曲榜",
				id:4395559,
				bg:require("./img/chinese_music.jpg")
			},
			{
				name: "我的专辑",
				id:98833242,
				bg:require("./img/my_music.jpg")
			},
		]
	};
  },
  computed:{
    
  },
  mounted:function(){
  },
  methods: {
    showList(index){
    	this.$router.push({ name: 'music-list', params: { id: this.albums[index].id}})
    }
  }
}
</script>
<style scoped lang="scss">
	#music{
		min-height: 100vh;
		box-sizing: border-box;
		padding: 64px 0 38px;
		text-align: center;
	}
	.md-layout{
		text-align:center;
		heihgt: 33.33%;

	}
	.album-img{
		width: 100%;
		height: 100%;
	}
</style>






